const catalogList = document.querySelector('.catalog-list');
const catalogMore = document.querySelector('.catalog__more');
const prodModal = document.querySelector('[data-graph-target = "prod-modal"] .graph-modal__content');
const prodModalSlider = prodModal.querySelector('.modal-slider .swiper-wrapper');
const prodModalPreview = prodModal.querySelector('.modal-slider .modal-preview');
const prodModalInfo = prodModal.querySelector('.modal-info__wrapper');
const prodModalDescr = prodModal.querySelector('.modal-prod-descr');
const prodModalChars = prodModal.querySelector('.prod-chars');
const prodModalVideo = prodModal.querySelector('.prod-modal__video');
let prodQuantity = 6;
let dataLength = null;

const normalPrice = (str) => {
  return String(str).replace(/(\d)(?=(\d\d\d)+([^\d]|$))/g, '$1 ');
};

const prodSlider = new Swiper('.modal-slider__container', {
  slidesPerView: 1,
  spaceBetween: 20
});


if (catalogList) {
  const loadProducts = (quantity = 9) => {
    fetch('../data/data.json')
      .then((response) => {
        return response.json();
      })
      .then((data) => {

        dataLength = data.length;
        catalogList.innerHTML = '';

        for (let i = 0; i < dataLength; i++) {
          if (i < quantity) {
            let item = data[i];

            catalogList.innerHTML += `

        <li class="catalog-list__item">
        <article class="product">
          <div class="product__image">
            <img src="${item.mainImage}" alt="${item.title}">
            <div class="product__btns">
              <button class="btn-reset product__btn" data-graph-path = "prod-modal" data-id="${item.id}" aria-label="Показать информацию о товаре">
                <svg width="30" height="30" viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path fill-rule="evenodd" clip-rule="evenodd" d="M18.9518 15.0664C18.9518 17.2489 17.1818 19.0176 14.9993 19.0176C12.8168 19.0176 11.0481 17.2489 11.0481 15.0664C11.0481 12.8826 12.8168 11.1139 14.9993 11.1139C17.1818 11.1139 18.9518 12.8826 18.9518 15.0664Z" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                  <path fill-rule="evenodd" clip-rule="evenodd" d="M14.9975 24.1936C19.7575 24.1936 24.1113 20.7711 26.5625 15.0661C24.1113 9.3611 19.7575 5.9386 14.9975 5.9386H15.0025C10.2425 5.9386 5.88875 9.3611 3.4375 15.0661C5.88875 20.7711 10.2425 24.1936 15.0025 24.1936H14.9975Z" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                  </svg>
              </button>
              <button data-id="${item.id}" class="btn-reset product__btn" aria-label="Добавить товар в корзину">
                <svg width="20" height="19" viewBox="0 0 20 19" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M20 6.57893H16.6038L13.0359 0.319309C12.8589 0.0083658 12.475 -0.093156 12.1784 0.0938088C11.8823 0.280774 11.7865 0.684867 11.9641 0.996475L15.1461 6.57893H4.85388L8.03587 0.996431C8.21348 0.684823 8.11767 0.280729 7.82164 0.0937645C7.5244 -0.0932003 7.14173 0.00832153 6.96411 0.319265L3.39617 6.57888H0V7.89468H1.35651L2.94432 16.8103C3.11033 17.7438 3.88547 18.421 4.78761 18.421H15.2124C16.1145 18.421 16.8896 17.7438 17.055 16.811L18.6434 7.89468H20C20 7.89468 20 6.57893 20 6.57893V6.57893ZM15.8264 16.5688C15.7715 16.8797 15.5133 17.1053 15.2124 17.1053H4.78761C4.4867 17.1053 4.22854 16.8798 4.173 16.5681L2.62789 7.89468H17.3721L15.8264 16.5688V16.5688Z" fill="white"/>
                  </svg>
              </button>
            </div>
          </div>
          <h3 class="product__title">${item.title}</h3>
          <span class="product__price">${normalPrice(item.price)} р</span>
        </article>
      </li>
        `
          }
        }

      })
      .then(() => {
        const productTitle = document.querySelectorAll('.product__title');
        productTitle.forEach((el) => {
          $clamp(el, { clamp: '22px' });
        });

        const modal = new GraphModal({
          isOpen: (modal) => {

            if (modal.modalContainer.classList.contains('prod-modal')) {

              const openBtnId = modal.previousActiveElement.dataset.id;

              loadModalData(openBtnId);

                    // prodSlider.update();
            }

          },
        });
      });
  };


  loadProducts(prodQuantity);

  const loadModalData = (id = 1) => {
    fetch('../data/data.json')
      .then((response) => {
        return response.json();
      })
      .then((data) => {

        prodModalSlider.innerHTML = '';
        prodModalPreview.innerHTML = '';
        prodModalInfo.innerHTML = '';
        prodModalDescr.textContent = '';
        prodModalChars.innerHTML = '';

        for (let dataItem of data) {
          if (dataItem.id == id) {
            const slides = dataItem.gallery.map(image => {
              return `
          <div class="swiper-slide">
          <img src="${image}" alt="">
        </div>
        `;
            });

            const preview = dataItem.gallery.map((image, idx) => {
              return `
          <div class="modal-preview__item" tabindex="0" data-index="${idx}">
              <img src="${image}" alt="">
            </div>
        `;
            });

            const sizes = dataItem.sizes.map((size, idx) => {
              return `
          <li class="modal-sizes__item">
          <button class="modal-sizes__btn btn-reset">${size}</button>
        </li>
        `;
            });

            prodModalSlider.innerHTML = slides.join('');
            prodModalPreview.innerHTML = preview.join('');
            prodModalInfo.innerHTML = `
        <h3 class="modal-info__title">${dataItem.title}</h3>
        <div class="modal-info__rate">
          <img src="img/star.png" alt="Рейтинг 5 из 5">
          <img src="img/star.png" alt="">
          <img src="img/star.png" alt="">
          <img src="img/star.png" alt="">
          <img src="img/star.png" alt="">
        </div>
        <div class="modal-info__sizes">
          <span class="modal-info__subtitle">Выберите размер</span>
          <ul class="list-reset modal-info__sizes-list modal-sizes">
        ${sizes.join('')}
          </ul>
        </div>
        <div class="modal-info__price">
          <span class="modal-info__current-price">${dataItem.price} р</span>
          <span class="modal-info__old-price">${dataItem.oldPrice ? dataItem.oldPrice + ' р' : ''}</span>
        </div>
        `
              ;
            prodModalDescr.textContent = dataItem.description;

            let charsItems = '';

            Object.keys(dataItem.chars).forEach(function eachKey(key) {
              charsItems += `<p class="prod-bottom__descr prod-chars__item">${key}: ${dataItem.chars[key]}</p>`
            });

            prodModalChars.innerHTML = charsItems;

            if (dataItem.video) {
              prodModalVideo.style.display = 'block';
              prodModalVideo.innerHTML = `
          <iframe src="${dataItem.video}"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen></iframe>
        `;
            } else {
              prodModalVideo.style.display = 'none';
            }



          }
        }
      });

  };

  catalogMore.addEventListener('click', (e) => {
    prodQuantity += 3;
    loadProducts(prodQuantity);

    if (prodQuantity >= dataLength) {
      catalogMore.style.display = 'none';
    } else {
      catalogMore.style.display = 'block';
    }

  });
}








